	import api from '../../common/api.js';
	export default {
		onLoad() {
			this.getserverList()
		},
		data() {
			return {
				serverList: [],
                
                ismore:false,
                contentText: {
                    contentdown: '查看更多',
                    contentrefresh: '加载中',
                    contentnomore: '没有更多',
                },
                page:1,
                size:10,
                mask:false,
                touchT:null,
                touchE:null,
                selectIndex:null,
			}
		},
        onPullDownRefresh() {
            console.log('onPullDownRefresh')
            this.page = 1
            this.getserverList()
            
        },
        onReachBottom() {
            console.log('onReachBottom')
            this.status = 'loading'
            uni.showNavigationBarLoading()
            
            this.page++
            this.getserverList()
        },
		methods: {
            clickDefault:function(item){
				api.settingDefaultCommunity({
					communityId: item.id
				}).then(res=>{
					this.page = 1
					this.getserverList()
				})
            },
            longtapServer:function(index){
                this.selectIndex = index
                this.mask = true
            },
            clickMask:function(){
                this.mask = !this.mask
            },
            touchend:function(){
                this.touchE = new Date().getTime();
            },
            touchstart:function(){
                this.touchT = new Date().getTime();
            },
            clickServer(index){
                if(this.touchE-this.touchT<350){
                    console.log(this.serverList[index])
                    uni.reLaunch({
                        url:'../index/index?communityid='+this.serverList[index].id
                    })
                }
            },
			getserverList() {
				// 获取服务站列表
				api.getmyjoincommunity().then(res=>{
                    if(res.status == 'OK'){
                        if(this.page == 1){
                            this.serverList = []
                            if(res.list.length>=this.size){
                                console.log('ismoreismoreismore')
                                this.ismore = true
                            }
                        }
                        res.list.map(item=>{
                            var update = false;
                            if(item.province){
                                if(item.province.length>2){
                                    item.addr = item.province.substring(0, 2)
                                    update = true
                                }else{
                                    item.addr = item.province
                                    update = true
                                }
                            }
                            if(item.city){
                                item.addr = item.addr  + ' | ' + item.city.substring(0,item.city.length - 1)
                                   update = true
                            }
                            if(!update){
                                item.addr = ''
                            }
                            if(item.icon && JSON.parse(item.icon ).length > 0){
                                item.icon = JSON.parse(item.icon)[0].url
                            }
                            if(item.score){
                                item.score = Math.round(item.score)
                            }
                            this.serverList.push(item);
                        })
                    }
                    uni.stopPullDownRefresh();
                    uni.hideNavigationBarLoading()
				})	
			},
			clickMarkBtn:function() {
                var item = this.serverList[this.selectIndex]
				api.canceljoincommunity({
					communityId: item.id 
				}).then(res=>{
                    this.mask = false
					this.getserverList()
				})
			},
			gomain: function () {
				uni.navigateTo({
					url: `/pages/serverStation/main`
				})
			},
			join(id) {
				api.joincommunity({
					communityId: id
				}).then(res=>{
					this.getserverList()
				})
			}
		}
	}
